#navigation-area {
  background-color: black;
  height: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:  center;
      justify-content:  center;
  position: fixed;
  width: 100vw;
  z-index: 100;
}

.nav-link {
  color: rgba(252, 252, 252, 0.8);
  line-height: 100px;
  display: inline;
  font-size: 1em;
  font-family: 'Lato';
  letter-spacing: .5em;
  padding: 0px 100px;
}

.nav-link:hover {
  color: white;
  text-align: center;
}

@media (max-width: 545px)  {
  .nav-link {
    font-size: .8em;
    letter-spacing: .2em;
    line-height: 80px;
  }
  #navigation-area {
    height: 80px;
    -ms-flex-align: center;
        align-items: center;
  }
}



